import React, { useRef, useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './logo.css';
import image1 from '../../images/b1.jpg'
import image2 from '../../images/b2.jpg'
import image3 from '../../images/b3.jpg'
import image4 from '../../images/b4.jpg'
import image5 from '../../images/b5.jpg'
import image6 from '../../images/b6.jpg'
import image7 from '../../images/b7.jpg'
import image8 from '../../images/b8.jpg'
import image9 from '../../images/b9.jpg'
import image10 from '../../images/b10.jpg'
import image11 from '../../images/b11.jpg'
import image12 from '../../images/b12.jpg'
import image13 from '../../images/b13.jpg'
import image14 from '../../images/b14.webp'
import image15 from '../../images/b15.jpg'
interface Brand {
  id: number;
  name: string;
  tagline: string;
  website: string;
  logo: string;
}

const BrandShowcase: React.FC = () => {
  // Initialize AOS
  useEffect(() => {
    AOS.init({
      duration: 800,
      once: true,
    });
  }, []);

  const brands: Brand[] = [
    {
      id: 1,
      name: "Emser",
      tagline: "Shaw Floors - Dream Weaver Collection",
      website: "https://www.emser.com/",
      logo: image1
    },
    {
      id: 2,
      name: "momentum",
      tagline: "Floors Designed to be Lived On",
      website: "https://www.momentumdesigns.com/",
      logo: image2
    },
    {
      id: 3,
      name: "andersontuftex",
      tagline: "Premium Building Materials & Solutions",
      website: "https://andersontuftex.com/en-us",
      logo:image3
    },
    {
      id: 4,
      name: "engineeredfloors",
      tagline: "Quality Surfaces for Inspired Spaces",
      website: "https://www.engineeredfloors.com/",
      logo: image4
    },
    {
      id: 5,
      name: "shawfloors",
      tagline: "Beautiful Surfaces for Your Home",
      website: "https://shawfloors.com/en-us",
      logo: image5
    },
    {
      id: 6,
      name: "mannington",
      tagline: "America's Tile Manufacturer",
      website: "https://www.mannington.com/home",
      logo: image6
    },
    {
      id: 7,
      name: "masonrycenter",
      tagline: "Quality Flooring for Every Budget",
      website: "https://masonrycenter.com/",
      logo: image7
    },
    {
      id: 8,
      name: "arizonatile",
      tagline: "Innovative Design Solutions",
      website: "https://arizonatile.com/",
      logo: image8
    },
     {
      id: 9,
      name: "maslandcarpets",
      tagline: "Innovative Design S",
      website: "https://www.maslandcarpets.com/",
      logo: image9
    },
     {
      id: 10,
      name: "floridatile",
      tagline: "Innovative Design Solutions",
      website: "https://www.floridatile.com/",
      logo: image10
    },
     {
      id: 11,
      name: "daltile",
      tagline: "Innovative Design Solutions",
      website: "https://www.daltile.com/",
      logo: image11
    },
     {
      id: 12,
      name: "dixie",
      tagline: "Innovative Design Solutions",
      website: "https://www.dixie-home.com/",
      logo: image12
    },
     {
      id: 13,
      name: "MOZAIK",
      tagline: "Innovative Design Solutions",
      website: "https://www.mozaiksc.com/",
      logo: image13
    },
     {
      id: 14,
      name: "Coretec",
      tagline: "Innovative Design Solutions",
      website: "https://coretecfloors.com/en-us",
      logo: image14
    },
     {
      id: 15,
      name: "Mohawk ",
      tagline: "Innovative Design Solutions",
      website: "https://www.mohawkflooring.com/",
      logo: image15
    }
  ];

  // Duplicate brands for seamless looping
  const duplicatedBrands = [...brands, ...brands];
  const sliderRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!sliderRef.current) return;

    const slider = sliderRef.current;
    let animation: Animation | null = null;

    const startAnimation = () => {
      if (!slider) return;
      
      // Get the width of one brand item
      const firstItem = slider.querySelector('.brand-item');
      if (!firstItem) return;
      
      const itemWidth = (firstItem as HTMLElement).offsetWidth;
      const totalWidth = duplicatedBrands.length * itemWidth;
      
      // Create keyframes
      const keyframes = [
        { transform: 'translateX(0)' },
        { transform: `translateX(-${totalWidth / 2}px)` }
      ];
      
      const options: KeyframeAnimationOptions = {
        duration: 40000,
        iterations: Infinity
      };
      
      animation = slider.animate(keyframes, options);
    };

    const stopAnimation = () => {
      if (animation) {
        animation.pause();
      }
    };

    const resumeAnimation = () => {
      if (animation) {
        animation.play();
      }
    };

    startAnimation();

    slider.addEventListener('mouseenter', stopAnimation);
    slider.addEventListener('mouseleave', resumeAnimation);

    return () => {
      if (animation) {
        animation.cancel();
      }
      slider.removeEventListener('mouseenter', stopAnimation);
      slider.removeEventListener('mouseleave', resumeAnimation);
    };
  }, [duplicatedBrands.length]);

  return (
    <div 
      className="brand-showcase"
      data-aos="fade-up"
    >
      <div 
        className="section-header"
        data-aos="fade-up"
        data-aos-delay="100"
      >
        <h2 className="section-title">Our Trusted Brands</h2>
        <p className="section-description">
          We partner with industry-leading manufacturers to bring you the highest quality materials
        </p>
      </div>
      
      <div className="slider-container">
        <div 
          className="slider-track" 
          ref={sliderRef}
          data-aos="fade-up"
          data-aos-delay="200"
        >
          {duplicatedBrands.map((brand, index) => (
            <div 
              className="brand-item" 
              key={`${brand.id}-${index}`}
              data-aos="zoom-in"
              data-aos-delay={300 + (index * 20)}
            >
              <div className="logo-container">
                <div className="logo-image">
                  <img 
                    src={brand.logo} 
                    alt={brand.name} 
                    className="brand-logo" 
                  />
                </div>
                <div className="brand-info">
                  <h3 className="brand-name">{brand.name}</h3>
                  <a 
                    href={brand.website} 
                    className="brand-link" 
                    target="_blank" 
                    rel="noopener noreferrer"
                  >
                    Visit Website <span className="external-icon"></span>
                  </a>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default BrandShowcase;