import React from 'react';
import { useDisclosure } from "@mantine/hooks";
import { Modal } from "@mantine/core";

function ModalLayout({ children }) {
  const [opened, { open, close }] = useDisclosure(false);

  return (
    <>
      <Modal 
        opened={opened} 
        onClose={close} 
        size="auto" 
        centered
        styles={{
          content: { 
            backgroundColor: 'transparent', 
            boxShadow: 'none',
            maxWidth: '90vw',
          },
          body: { padding: 0 }
        }}
      >
        {children}
      </Modal>

      <button 
        onClick={open}
        style={{
          padding: '16px 40px',
          borderRadius: '50px',
          fontWeight: 600,
          fontSize: '1.1rem',
          textDecoration: 'none',
          transition: 'all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1)',
          boxShadow: '0 5px 15px rgba(0, 0, 0, 0.2)',
          display: 'inline-block',
          background: 'linear-gradient(to right, #5a6a72, #36434A)',
          color: '#f8f9fa',
          border: 'none',
          cursor: 'pointer',
          fontFamily: 'inherit',
        }}
        onMouseOver={(e) => {
          e.currentTarget.style.background = 'linear-gradient(to right, #36434A, #2a353b)';
          e.currentTarget.style.transform = 'translateY(-3px)';
          e.currentTarget.style.boxShadow = '0 8px 20px rgba(0, 0, 0, 0.3)';
        }}
        onMouseOut={(e) => {
          e.currentTarget.style.background = 'linear-gradient(to right, #5a6a72, #36434A)';
          e.currentTarget.style.transform = 'translateY(0)';
          e.currentTarget.style.boxShadow = '0 5px 15px rgba(0, 0, 0, 0.2)';
        }}
        onMouseDown={(e) => {
          e.currentTarget.style.transform = 'translateY(1px)';
          e.currentTarget.style.boxShadow = '0 3px 10px rgba(0, 0, 0, 0.2)';
        }}
        onMouseUp={(e) => {
          e.currentTarget.style.transform = 'translateY(-3px)';
          e.currentTarget.style.boxShadow = '0 8px 20px rgba(0, 0, 0, 0.3)';
        }}
      >
        View All Reviews
      </button>

      <style>
        {`
          @media (max-width: 768px) {
            button {
              padding: 14px 32px !important;
              font-size: 1rem !important;
            }
          }
          
          @media (max-width: 480px) {
            button {
              padding: 12px 28px !important;
              font-size: 0.95rem !important;
            }
          }
        `}
      </style>
    </>
  );
}

export default ModalLayout;