// Updated ProjectGallery.tsx
import React, { useState, useEffect, useCallback } from 'react';
import { FiChevronLeft, FiChevronRight, FiX } from 'react-icons/fi';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './project.css';
import image1 from '../../images/ideal-floors-001.jpg';
import image2 from '../../images/ideal-floors-002.jpg';
import image3 from '../../images/ideal-floors-003.jpg';
import image4 from '../../images/ideal-floors-004.jpg';
import image5 from '../../images/ideal-floors-005.jpg';
import image6 from '../../images/ideal-floors-006.jpg';
import image7 from '../../images/ideal-floors-007.jpg';

const ProjectGallery: React.FC = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [lightboxIndex, setLightboxIndex] = useState(0);
  const [projectsToShow, setProjectsToShow] = useState(4);

  const projects = [
    { id: 1, title: 'Modern Office Design', imageUrl: image1 },
    { id: 2, title: 'Architectural Concept', imageUrl: image2 },
    { id: 3, title: 'Luxury Car', imageUrl: image3 },
    { id: 4, title: 'Modern House', imageUrl: image4 },
    { id: 5, title: 'Interior Design', imageUrl: image5 },
    { id: 6, title: 'Tech Product', imageUrl: image6 },
    { id: 7, title: 'Landscape Design', imageUrl: image7 },
  ];

  useEffect(() => {
    AOS.init({ duration: 800, once: true });
  }, []);

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 768) {
        setProjectsToShow(1);
      } else if (window.innerWidth < 992) {
        setProjectsToShow(2);
      } else if (window.innerWidth < 1200) {
        setProjectsToShow(3);
      } else {
        setProjectsToShow(4);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const chunkArray = (arr: typeof projects, size: number) => {
    const result = [];
    for (let i = 0; i < arr.length; i += size) {
      result.push(arr.slice(i, i + size));
    }
    return result;
  };

  const projectChunks = chunkArray(projects, projectsToShow);
  const totalSlides = projectChunks.length;

  const nextSlide = useCallback(() => {
    setCurrentSlide((prev) => (prev === totalSlides - 1 ? 0 : prev + 1));
  }, [totalSlides]);

  const prevSlide = useCallback(() => {
    setCurrentSlide((prev) => (prev === 0 ? totalSlides - 1 : prev - 1));
  }, [totalSlides]);

  const openLightbox = (index: number) => {
    setLightboxIndex(index);
    setLightboxOpen(true);
    document.body.style.overflow = 'hidden';
  };

  const closeLightbox = () => {
    setLightboxOpen(false);
    document.body.style.overflow = 'auto';
  };

  const nextLightbox = () => {
    setLightboxIndex((prev) => (prev === projects.length - 1 ? 0 : prev + 1));
  };

  const prevLightbox = () => {
    setLightboxIndex((prev) => (prev === 0 ? projects.length - 1 : prev - 1));
  };

  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!lightboxOpen) return;
      if (e.key === 'Escape') closeLightbox();
      else if (e.key === 'ArrowRight') nextLightbox();
      else if (e.key === 'ArrowLeft') prevLightbox();
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [lightboxOpen]);

  useEffect(() => {
    if (lightboxOpen) return;
    const interval = setInterval(() => {
      nextSlide();
    }, 5000);
    return () => clearInterval(interval);
  }, [lightboxOpen, nextSlide]);

  return (
    <section id="gallery" className="gallery-section">
      <div className="container">
        <div className="section-header" data-aos="fade-up">
          <h2>Our Work</h2>
          <p data-aos="fade-up" data-aos-delay="100">
            Check out our gallery and discover the superior quality of our outstanding work.
          </p>
        </div>

        <div className="gallery-container">
          <button className="gallery-nav prev" onClick={prevSlide}>
            <FiChevronLeft />
          </button>

          <div
            className="gallery-slider"
            style={{
              width: `${projectChunks.length * 100}%`,
              transform: `translateX(-${currentSlide * (100 / projectChunks.length)}%)`,
              display: 'flex',
            }}
          >
            {projectChunks.map((chunk, chunkIndex) => (
              <div
                key={chunkIndex}
                className="gallery-slide"
                style={{
                  width: `${100 / projectChunks.length}%`,
                  display: 'flex',
                  gap: '20px',
                  justifyContent: 'center',
                }}
              >
                {chunk.map((project, index) => (
                  <div
                    key={project.id}
                    className="gallery-item"
                    style={{ flex: `0 0 calc(${100 / projectsToShow}% - 20px)` }}
                  >
                    <img src={project.imageUrl} alt={project.title} />
                    <div className="gallery-overlay">
                      <button
                        className="preview-btn"
                        onClick={() => openLightbox(chunkIndex * projectsToShow + index)}
                      >
                        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                          <path d="M15 12a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
                          <path d="M12 4.5C7 4.5 2.7 7.6 1 12c1.7 4.4 6 7.5 11 7.5s9.3-3.1 11-7.5c-1.7-4.4-6-7.5-11-7.5z" />
                        </svg>
                        Preview
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            ))}
          </div>

          <button className="gallery-nav next" onClick={nextSlide}>
            <FiChevronRight />
          </button>
        </div>

        <div className="gallery-dots">
          {Array.from({ length: totalSlides }).map((_, index) => (
            <button
              key={index}
              className={`dot ${index === currentSlide ? 'active' : ''}`}
              onClick={() => setCurrentSlide(index)}
              aria-label={`Go to slide ${index + 1}`}
            />
          ))}
        </div>
      </div>

      {lightboxOpen && (
        <div className="lightbox active" onClick={closeLightbox}>
          <div className="lightbox-content" onClick={(e) => e.stopPropagation()}>
            <button className="lightbox-close" onClick={closeLightbox}>
              <FiX />
            </button>
            <button className="lightbox-nav lightbox-prev" onClick={prevLightbox}>
              <FiChevronLeft />
            </button>
            <img src={projects[lightboxIndex]?.imageUrl} alt={projects[lightboxIndex]?.title} />
            <button className="lightbox-nav lightbox-next" onClick={nextLightbox}>
              <FiChevronRight />
            </button>
          </div>
        </div>
      )}
    </section>
  );
};

export default ProjectGallery;
